































































































































import { AppService } from "@/service/app.service";
import { RequestData } from "@/x-vue/services/interfaces";
import Vue from "vue";
import Component from "vue-class-component";
import UploadImage from "@/x-vue/components/file/UploadImage.vue";
@Component({
  components: { UploadImage },
})
export default class CafeAdminEdit extends Vue {
  app = AppService.instance;

  async onSubmit(): Promise<void> {
    const options: RequestData = {
      idx: this.$store.state.cafe.idx,
      title: this.$store.state.cafe.title,
      description: this.$store.state.cafe.description,
      subcategories: this.$store.state.cafe.subcategories,
      app_name: this.$store.state.cafe.app_name,
      app_background_color: this.$store.state.cafe.app_background_color,
    };

    this.$store.commit("cafeUpdate", options);
  }

  refresh(): void {
    this.$store.commit("loadCafe", this.app.cafeDomain);
  }
}
